/* Google Font Import Link */
@import url('https://fonts.googleapis.com/css2?family=Ubuntu:wght@400;500;700&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
  font-family: 'Ubuntu', sans-serif;
}

body {
  margin: 0;
  padding: 0;
}

.projects {
  background-color: #f7f7f7;
  padding-top: 70px;
  padding-bottom: 70px;
  padding-left: 70px;
  padding-right: 70px;
  text-align: center;
}

.heading-container {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 20px;
  animation: slideTopToBottom 1.5s ease-in-out;
}

.icon {
  width: 30px;
  margin-right: 10px;
}

.heading {
  font-size: 2em;
  color: #333;
}

.heading span {
  color: #f18048;
}

.description {
  font-size: 1.2em;
  color: #666;
  margin-bottom: 40px;
}

.project-cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.project-card {
  background-color: #fff;
  width: calc(33.333% - 20px);
  margin-bottom: 30px;
  border-radius: 8px;
  text-align: center;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.project-card.animate {
  opacity: 1;
  transform: translateY(0);
}

.project-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

.project-image {
  width: 100%;
  height: auto;
  border: 1px solid #333;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
}

.project-details {
  padding: 20px;
  text-align: left;
}

.project-title {
  color: #333;
  margin-bottom: 10px;
  font-size: 1.5em;
}

.project-description {
  color: #666;
  font-size: 1em;
  line-height: 1.6;
}

@media only screen and (max-width: 1024px) {
  .project-card {
    width: calc(50% - 20px);
  }
}

@media only screen and (max-width: 768px) {
  .projects {
    padding: 20px;
  }

  .project-card {
    width: calc(50% - 10px);
  }

  .heading {
    font-size: 1.5em;
  }

  .description {
    font-size: 1em;
  }
}

@media only screen and (max-width: 600px) {
  .project-card {
    width: calc(100% - 20px);
  }
}
